import {useState} from 'react';
import {useHistory} from 'react-router-dom';


const Form=()=>{const [a,b]=useState(""); const history=useHistory();const d=async (w)=>{w.preventDefault(); const am=await fetch('/save',{method:"POST",headers:{'Content-Type':'application/json'},body:JSON.stringify({q:a})}); const bm=await am.json(); b(""); alert(bm.ok);history.push('/view');};return(<div><form method="POST" autoComplete="off" onSubmit={d}><label>Post something</label><textarea rows={13} cols={33}  style={{padding:"15px"}} onChange={(d)=>b(d.target.value)} /><input type="submit" value="save"/></form><footer>hi there</footer></div>); };

export default Form;