import {useState,useEffect} from 'react';
import {Link} from 'react-router-dom';
const Viewer=()=>{const [all,zee]=useState([]);const getall=async()=>{const t=await fetch('/getall',{method:'GET'}); const dp=await t.json();  if(dp.arr.length===0){return;} zee(dp.arr);}; useEffect(()=>getall(),[]); return(<div><section><Link to="/" ><h1>Back to index page</h1></Link></section><section>{(all.length!==0) && all.map(w=><blockquote><q>{w.quote}</q></blockquote>)}</section></div>);};


export default Viewer;